nav {
    background: rgba(0, 0, 0, 0.3);
    width: max-content;
    display: block;
    padding: 1rem 2rem;
    z-index: 2;
    position: fixed;
    left: 50%;
    transform: translateX(-50%);
    bottom: 2rem;
    display: flex;
    gap: 0.8rem;
    border-radius: 0.4rem;
    backdrop-filter: blur(15px);
}
  
nav a {
    background: transparent;
    padding: 1rem;
    display: flex;
    color: var(--lightColor);
}

nav a:hover {
    background: rgba(0, 0, 0, 0.3);
}